* {
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.app {
  text-align: center;
}

.title {
  font-family: 'Indie Flower';
  margin: 0px 0px 15px 0px;
  padding: 20px 0px;
}

.powered-by {
  font-size: 16px;
  font-style: italic;
  margin-left: 20px;
}

.current-tag {
  font-size: 24px;
  margin: 20px 0px;
}

.table-container {
  width: 600px;
  height :300px;
  max-width: 80%;
  margin: 0 auto;
}

.left, .right {
  width: 49%;
  min-height: 300px;
}

.container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.reserve {
  margin: 20px 0px;
}

.reserve-tag {
  margin-right: 10px;
  font-size: 16px;
}

@media screen and (max-width: 800px) {
  .left, .right {
    width: 100%;
    min-height: 200px;
  }
  .powered-by {
    display: block;
  }
}
